
#app{height:100%;width:100%}
.index{
	height:100%;
	@include flexbox();
	@include flex-direction(column);

}
header{
	height:.44rem;
	background:#4be137;
	position:relative;
	h2{
		color:#fff;
		text-align:center;
		line-height:.44rem;
		font-size:.16rem;
		letter-spacing:4px
	}
	span{
		position:absolute;

	}
	.z-right{right:10px;top:0px;
		width:44px;
		height:44px;
		display:block;
		text-align:center;
		line-height:.44rem;
		color:#fff;
		font-size:.16rem

		}
	.z-left{left:10px;top:7px;
		width:.45rem;
		height:.27rem;
		display:block;
		img{
			width:100%;
			height:100%
		}
	}
}
section{
    width:100%;
	overflow:hidden;
	overflow-y:auto;
	flex:1;

}
nav{
	height:.44rem;
	
	ul{
	  @include flexbox();
	  width:100%;
	  height:100%;
	  li{
	    height:100%;
	    width:25%;
	    a{

		    @include flexbox();
		    @include flex-direction(column);
		    @include justify-content(center);
		    @include align-items(center)
		    color:grey;
		    	span{
	        	
	        	font-size:.14rem;
        	 }
	    }
	    &.active span{color:green}
        
	  }
	}
}




